import { NextResponse } from "next/server";
import prisma from "@/lib/prisma";
import jwt from "jsonwebtoken";

export async function POST(req) {
  console.log("üì• /addPoints API CALLED");

  try {
    const body = await req.json();
    console.log("üì¶ Received body:", body);

    const { points } = body;

    // Check body
    if (!points) {
      console.log("‚ùå No points sent");
      return NextResponse.json(
        { error: "No points provided" },
        { status: 400 }
      );
    }

    // Read cookie
    const cookie = req.headers.get("cookie") || "";
    console.log("üç™ Cookie header:", cookie);

    const match = cookie.match(/token=([^;]+)/);
    if (!match) {
      console.log("‚ùå No token found in cookie");
      return NextResponse.json({ error: "No token" }, { status: 401 });
    }

    const token = match[1];
    console.log("üîë Extracted token:", token);

    // Decode token
    let decoded;
    try {
      decoded = jwt.verify(token, process.env.JWT_SECRET || "your-secret-key");
      console.log("üß© Decoded token:", decoded);
    } catch (err) {
      console.log("‚ùå Token verification failed:", err);
      return NextResponse.json({ error: "Invalid token" }, { status: 401 });
    }

    // Fetch user from DB
    const user = await prisma.user.findUnique({
      where: { id: decoded.userId },
    });

    console.log("üë§ Fetched user:", user);

    if (!user) {
      console.log("‚ùå User not found in database");
      return NextResponse.json({ error: "User not found" }, { status: 404 });
    }

    // Update points
    const updated = await prisma.user.update({
      where: { id: user.id },
      data: { points: user.points + points },
    });

    console.log("‚úÖ Updated user:", updated);

    return NextResponse.json({
      user: { email: updated.email, points: updated.points },
    });
  } catch (err) {
    console.log("üî• SERVER ERROR:", err);
    return NextResponse.json({ error: "Server error" }, { status: 500 });
  }
}
